import React, { useState, useEffect } from 'react'
import Navbar from './Navbar'
import { Button, TextField } from '@mui/material';
import { db, auth } from '../firebase';
import { useNavigate } from 'react-router-dom';

const Addnote = ({ user }) => {
    const [text, settext] = useState("");
    const [todos, settodos] = useState([]);
    const addTodo = () => {
        const date = new Date();
        const time = date.getTime();
        const counter = time;
        const id = counter;
        console.log(id);
        if (user) {
            db.collection(user.uid).doc("_" + id).set({
                id: "_" + id,
                Title: text
            }).then(settext("")).catch(err => console.log(err.message))
            console.log("Added todo")
        } else {
            console.log("No user")
        }
    }

    useEffect(() => {
        auth.onAuthStateChanged(user => {
            if (user) {
                const todoList = todos;
                db.collection(user.uid).onSnapshot(snapshot => {
                    let changes = snapshot.docChanges();
                    changes.forEach(change => {
                        if (change.type === 'added') {
                            todoList.push({
                                id: change.doc.id,
                                Title: change.doc.data().Title
                            })
                        }
                        if (change.type === 'removed') {
                            // console.log(change.type);
                            for (var i = 0; i < todoList.length; i++) {
                                if (todoList[i].id === change.doc.id) {
                                    todoList.splice(i, 1);
                                }
                            }
                        }
                        settodos(todoList)
                    })
                })
            }
            else {
                console.log('user is not signed in to retrive todos');
            }
            console.log(todos);
        })
    }, []);
    // const display = () => {

    // }
    return (
        <>
            <Navbar color="black" />
            <h1>Addnote</h1>
            <div className="d-flex">
                <TextField className="mx-2" autoComplete="off" type="text" id="standard-basic" variant="standard" value={text} onChange={(e) => { settext(e.target.value) }} />
                {/* <TextField type="text" id="standard-basic" variant="standard" style={{ marginTop: "2px" }} value={notedesc} onChange={(e) => { setnotedesc(e.target.value) }} /> */}
            </div>
            <Button className="my-3" variant="contained" color="success" style={{ borderRadius: "25px", width: "20%" }} onClick={addTodo}>Add note</Button>
            {todos.map((todos) => {
                return (
                    <>
                        <div className="row"> <h3 className="card-title">{todos.Title}</h3></div>

                    </>
                )
            })}
        </>
    )
}
export default Addnote